import React, {useEffect, useState} from 'react';

const srcText = [
  'I Love You chiu chiu...',
  'Anh yu cngggggg',
  'Chng mnh s cng yu nhau hn',
  'Em khng thot c u =)))',
  'Em l ca anh...',
  'Gin di anh t thi, khng anh hn y =)))',
  'Anh yu Dung nhiu lm S2'
];

function randomText() {
  return srcText[Math.floor(Math.random() * srcText.length)];
}
export default function Typo() {
  const [text, setText] = useState(randomText());
  const [typing, setTyping] = useState('');
  useEffect(() => {
    setTimeout(() => {
      for (let i = 0; i <= text.length; i++) {
        setTimeout(() => {
          let result = text.slice(0, i);
          setTyping(result);
          if (i === text.length) {
            setText((text) => randomText());
          }
        }, 98 * i);
      }
    }, text.length * 98);
  }, [text]);
  return (
    <h2 className={'typing pointer'} id={'typing'}>
      {typing}
    </h2>
  );
}
